
.side-menu {
  z-index: -1;
  font-size: 32px;
  display: none;
}
.side-menu,
.side-menu .mask {
  -webkit-transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
          transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.side-menu .mask {
  position: absolute;
}
.side-menu .menu-inner {
  width: 50%;
  left: -50%;
  height: 100%;
  position: absolute;
  z-index: 100;
  background-color: #fff;
  -webkit-transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
          transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}
.side-menu.show {
  z-index: 99;
  opacity: 1;
}
.side-menu.show .menu-inner {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.side-menu.show .mask {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.7);
}
.side-menu li {
  line-height: 100px;
  border-bottom: 1px solid #f0f0f0;
  padding-left: 42%;
  position: relative;
}
.side-menu li .icon {
  position: absolute;
  left: -60px;
  top: 50%;
  margin-top: -26px;
}
.side-menu li.on a {
  color: #fb3f3d;
}
.side-menu li.on .icon-home:before {
  background-position: 14px -110px;
}
.side-menu li.on .icon-menu-event:before {
  background-position: -46px -110px;
}
.side-menu li.on .icon-xinping:before {
  background-position: -110px -110px;
}
.side-menu li.on .icon-discount:before {
  background-position: -170px -110px;
}
.side-menu li.on .icon-list:before {
  background-position: -228px -110px;
}
.side-menu li.on .icon-experience:before {
  background-position: -286px -110px;
}
.side-menu li.on .icon-uee:before {
  background-position: -108px -544px;
}
.side-menu li.on .icon-mall:before {
  background-position: -348px -110px;
}
.side-menu li.on .icon-contact:before {
  background-position: -410px -110px;
}
.side-menu li.on .icon-app-download:before {
  background-position: -404px -410px;
}
.side-menu a {
  color: #cfcfcf;
  display: block;
  position: relative;
}
.icon-app-down-arrow {
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -26px;
}
.menu-app-download {
  padding: 30px;
  position: relative;
}
.menu-app-download .app-download-logo {
  width: 84px;
}
.menu-app-download .app-download-desc {
  margin-left: 100px;
}
.menu-app-download .app-download-logo img {
  width: 100%;
}
.menu-app-download + ul {
  border-top: 1px solid #f0f0f0;
}



