.Ripple {
  position: absolute;
  background: rgba(0, 0, 0, .1);
  border-radius: 50%;
  transform: scale(0);

  &.is-reppling {
    animation: ripple 0.5s linear; }

  &-parent {
    // Parent Position Should be Relative, Absolute or Fixed Not Static
    position: relative;
    // position: absolute
    // position: fixed
    overflow: hidden; } }

@keyframes ripple {
  100% {
    opacity: 0;
    transform: scale(3); } }



